let MauBinhRequest = {};
MauBinhRequest.REQUEST_NAME = {
    SIT_ON_REQUEST : "mb1",
    SIT_OUT_REQUEST : "mb2",
    STAND_UP_REQUEST : "mb3",
    XEP_XONG_REQUEST: "mb10",
    BAO_BINH_REQUEST: "mb11",
}
//
MauBinhRequest._BaseRequest = SmartFoxSDK.Class.extend({
    ctor(name){
        this._requestName = name;
        this._params = new SmartFoxSDK.SObject();
        this._roomId = undefined;
    },
    setRoomId(id){
        this._roomId = id;
        return this;
    },
    getRequestName(){
        return this._requestName;
    },
    toSRequest(){
        return new SmartFoxSDK.SmartFox.Requests.System.ExtensionRequest(this._requestName, this._params, this._roomId);
    }
});
//
MauBinhRequest.SitOnRequest = MauBinhRequest._BaseRequest.extend({
    ctor(){
        this._super(MauBinhRequest.REQUEST_NAME.SIT_ON_REQUEST);
    },
    setDeskId(id){
        this._params.putInt(this.constructor.DESK_ID, id);
        return this;
    },
    setAutoSitOn(){
        this._params.putInt(this.constructor.AUTO_SIT_ON, -10);
        return this;
    }
});
MauBinhRequest.SitOnRequest.DESK_ID = "id";
MauBinhRequest.SitOnRequest.AUTO_SIT_ON = "as";
//
MauBinhRequest.SitOutRequest = MauBinhRequest._BaseRequest.extend({
    ctor(){
        this._super(MauBinhRequest.REQUEST_NAME.SIT_OUT_REQUEST);
    },
    setSitOut(sit_out){
        this._params.putBool(this.constructor.IS_SIT_OUT, sit_out);
        return this;
    }
});
MauBinhRequest.SitOutRequest.IS_SIT_OUT = "iso";
//
MauBinhRequest.StandUpRequest = MauBinhRequest._BaseRequest.extend({
    ctor(){
        this._super(MauBinhRequest.REQUEST_NAME.STAND_UP_REQUEST);
    }
});
MauBinhRequest.XepXongRequest = MauBinhRequest._BaseRequest.extend({
    ctor(){
        this._super(MauBinhRequest.REQUEST_NAME.XEP_XONG_REQUEST);
    },
    setCards(cards){
        this._params.putByteArray("lc",cards);
        return this;
    }
})
MauBinhRequest.BaoBinhRequest = MauBinhRequest._BaseRequest.extend({
    ctor(){
        this._super(MauBinhRequest.REQUEST_NAME.BAO_BINH_REQUEST);
    },
    setCards(cards){
        this._params.putByteArray("lc",cards);
        return this;
    }
})
window.MauBinhRequest = MauBinhRequest;
